import React from 'react';
import './css/navbar.css';


function Navbar(props) {
    return ( 
    	<> 
    	<nav>
		    <div class="nav-wrapper  blue darken-3">
		      <a href="#" class="brand-logo center "><i class="material-icons">person</i>{props.title}</a>
		    </div>
  		</nav>
  		</> 
    	);
}
export default Navbar;