import React from 'react';
import {
  Link
} from "react-router-dom";
import './css/users.css';
import Navbar from './Navbar.js'


function Users(props) {
    return ( 
    	<> 
      <Navbar title={props.title}/>
    	<div className = 'container'>
    	 <table className="striped highlight responsive-table centered">
        <thead className="grey darken-3">
          <tr>
              <th>Name</th>
              <th>Email</th>
              <th>Gender</th>
              <th>Contact No.</th>
              <th>Address</th>
              <th>Action</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Rajan</td>
            <td>rajan@gmail.com</td>
            <td>Male</td>
            <td>902930474</td>
            <td>Sirsa</td>
            <td>
              <Link to="/update-user"><i class="material-icons">create</i></Link>
              &nbsp; &nbsp;<i class="material-icons">delete</i></td>
          </tr>
        </tbody>
      </table>
      <div className='new'>
         <Link to="/new-user" class="waves-effect waves-light btn blue darken-3 hoverable">Add New User</Link>
      </div>
      </div>
    	</> 
    	);
}
export default Users;