import React from 'react';
import {
  Link
} from "react-router-dom";
import './css/form.css';
import Navbar from './Navbar.js'


function Form(props) {
    return ( 
    	<> 
      <Navbar title={props.title}/>
    	<div className = 'container'>
      <div>
        <Link to="/" class="allbtn right">
          <i class="material-icons">arrow_back</i> <br/>All Users
        </Link>
      </div>
        <form class="col s12" method='POST'>
          <div class="row">
            <div class="input-field col s12">
              <input id="name" type="text" class="validate" name='name'/>
              <label for="name">Name</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input id="email" type="email" class="validate" name='email'/>
              <label for="email">Email</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s6">
              <input id="gender" type="text" class="validate" name='gender'/>
              <label for="gender">Gender</label>
            </div>
            <div class="input-field col s6">
              <input  id="contact" type="text" class="validate" name='contact'/>
              <label for="contact">Contact no.</label>
            </div>
          </div>
          <div class="row">
            <div class="input-field col s12">
              <input id="address" type="text" class="validate" name='address'/>
              <label for="address">Address</label>
            </div>
          </div>
          <div class="row">
            <div class="col s12">
               
                <input class="waves-effect waves-light btn blue darken-3 hoverable" type='submit' value={props.action}/>
               
            </div>
          </div>
        </form>
      </div>
      
    	</> 
    	);
}
export default Form;