import React from 'react';
import './App.css';
import {
	BrowserRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Users from './components/Users.js'
import Form from './components/Form.js'

function App() {
    return ( 
    	<> 
    	<BrowserRouter>
	    	<Switch>
	          <Route exact path="/" >
	            <Users title='User Management System'/>
	          </Route>
	          <Route path="/new-user">
	            <Form title='New User' action='Create New'/>
	          </Route>
	          <Route path="/update-user">
	            <Form title='Update User' action='Update'/>
	          </Route>
	        </Switch>
		</BrowserRouter> 
    	
    	
    	</> 
    	);
}
export default App;